.whitebox {
  border-radius: 10px;
  margin: 10px 10px 10px 10px;
  height: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  border-radius: 20px;
}

.heart {
  width: 9.3%;
  height: 40px;
  background: url(icons8-하트-48.png);
  margin:20px;
}
.firstday {
  text-align: center;
  width: 200px;
  margin-bottom: 10px;
}
.day-show-div {
  display: flex;
}
.day-show-div > .leftday > .day {
  float: left;
  margin : 20px 25px 20px 0px;
}
.day-show-div > .rightday > .day {
  margin: 20px 0px 20px 25px;
  float :right;
}
.line2 {
  width: 100%;
  height: 0.5px;
  background: #767676;
  border-radius: 50px;
}